<template>
  <div>
    <h1>题目列表</h1>
    <table>
      <thead>
        <tr>
          <th>ID</th>
          <th>题目</th>
          <th>题目类型</th>
          <th>创建时间</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="question in questions" :key="question.dataId">
          <td>{{ question.dataId }}</td>
          <td>{{ question.question }}</td>
          <td>{{ getQuestionTypeCN(question.questionType) }}</td>
          <td>{{ question.entryDate }}</td>
        </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
import axios from 'axios'
import { questionTypeMap } from '@/utils/questionTypes'

export default {
  name: 'QuestionList',
  data() {
    return {
      questions: []
    }
  },
  created() {
    this.fetchQuestions()
  },
  methods: {
    // 转换类型为中文
    getQuestionTypeCN(enType) {
      return questionTypeMap[enType] || enType // 未知类型显示原值
    },
    fetchQuestions() {
      axios.get('/api/select')
        .then(response => {
          console.log('后端返回数据:', response.data)
          this.questions = response.data
        })
        .catch(error => {
          console.error('获取题目失败:', error)
        })
    }
  }
}
</script>

<style scoped>
table {
  width: 80%;
  margin: 20px auto;
  border-collapse: collapse;
}

th, td {
  border: 1px solid #ddd;
  padding: 8px;
  text-align: left;
}

th {
  background-color: #f2f2f2;
}

tr:nth-child(even) {
  background-color: #f9f9f9;
}
</style>